import React, { Component } from "react";
// import Navbar from "./components/Navbar";
import CharacterPics from "./components/CharacterPics";
import Wrapper from "./components/Wrapper";
import characters from "./characters.json";

class Game extends Component {
    // Setting this.state.characters to the characters json array
    state = {
        characters,
        selected: false
    };

    characterSelect = () => {
        if (this.state.selected) {
            console.log(`Character has been selected = ${this.state.selected}`)
        } else {
            this.setState({
                selected: true
            })
        }
        console.log(this.state.selected);
    };

    // Map over this.state.characters and render a CharacterPics component for each friend object
    render() {
        return (
            <Wrapper>
                {this.state.characters.map(characters => (
                    <CharacterPics
                        characterSelect={this.characterSelect}
                        id={characters.id}
                        key={characters.id}
                        // name={characters.name}
                        image={characters.image}
                        selected={characters.selected}
                        // value={this.state.selected}
                        // onClick={this.characterSelect}
                    />
                ))}
            </Wrapper>
        );
    }
}

export default Game;
